*{
  margin:0;
  padding:0;
}

.header{
  display: flex;
  flex-direction:row;
  background-color:#290402;
  color:grey;
  padding:1.5rem;
}

.header h1{
  align-content: center;
  text-align: center;
  flex:1;
}

body{
  line-height:1.4;
}

h1{
  font-size: 3rem;
}

button{
  background:#333;
  color:#fff;
  padding:1rem 2rem;
  border:none;
  text-transform:'uppercase';
  margin:15px 0;
  cursor: pointer;
}

.play-game{
  background-color:brown;
  text-align: center;
}

.game{
  text-align:center;
}

.game2{
  justify-content:center;
  text-align: center;
  display: flex;
  flex-direction:row;
}

#lightbox {
	position: absolute;
	z-index: 1000;
	top:0;
	height:100%;
	width:100%;
	background-color: rgba(0,0,0, .8);
	display:none;
}
/*These styles will apply once the lightbox has a class of active*/
#lightbox.active{
	display:flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color:white;
}

/*This will ensure that the image never takes up the full space of the browser*/
#lightbox img{
	max-width:90%;
	max-height:90%;
}

#lightbox button{
  background-color: grey;
}

#lightbox2 {
	position: absolute;
